import React from 'react';
import './About.css';

function AboutBody() {
    return (
        <div className='about'>
            <div className='about__container'>
                <div className='about__wrapper'>
                    <section>
                    <h2>Magamról, kicsit <br />hosszabban</h2>
                    <h3 className='about__leftbox'>Csákány Krisztián vagyok, 24 éves. Egészen pontosan 1997. július 18-án születtem, a zentai kórházban, Szerbiában. <br />
                    Szintén egy észak szerbiai településen: Horgoson nevelkedtem egészen 21 éves koromig. Itt jártam óvodába és álltalános iskolába is. <br />
                    A Gépésztechnikus – Számítógépes szerkesztő szakközépiskolát a szomszédos településen, Magyarkanizsán végeztem el a Beszédes József Mezőgazdasági és Műszaki Iskola Központban.<br /><br /><br /></h3>
                    <h2>Az első munka</h2>
                    <h3 className='about__leftbox'>Az érettségi után sajnos nem tudtam egyből munkába állni, nagymamám megbetegedése miatt otthon maradtam, hogy tudjak segédkezni. Csak 2016 őszén tudtam elkezdeni dolgozni. <br />
                    Ekkor kőműves inasnak álltam be nagyapám kezei alá. A falun belül dolgozgattunk különböző kőműves munkákat. <br />
                    Gyerekkorom óta közel állt hozzám az építgetés, ugyanis már kiskorunkban unokabátyámmal nagyszüleink hátsó udvarában építettünk különböző bunkereket. <br />
                    Korábban már dolgoztam nagyapámmal a középiskola nyári szüneteiben, így nem volt már újdonság a munka.<br /><br /><br /></h3>
                    <h2>Mercecdes Benz kalandok</h2>
                    <h3 className='about__leftbox'>2018 nyarán alakultak úgy a dolgok, hogy szembe találkoztam egy hirdetéssel, amiben munkaerőt kerestek a kecskeméti Mercedes Benz Manufacturing Hungary Kft.-be. <br />
                    A középiskolában még tervben volt, hogy egyszer, egyetem után ott dolgozzam, igaz akkor inkább mérnökként, de így is megörültem a lehetőségnek. <br />
                    A karosszéria lakatosok közé kerültem, hála iskolai végzettségemnek, motorháztető, csomagtérajtó és utastér ajtóállításra. <br />
                    Sajnos a hirtelen távollét, a honvágy és a magány miatt nem bírtam túl sokáig a családom nélkül, így másfél hónap után hazautaztam, felmondva a gyári munkát. <br />
                    Ekkor kellett szembesülnöm a ténnyel, hogy a szalagmunka egyelőre biztosan nem az én világom. Úgy éreztem, többre is képes vagyok a monoton munkánál. <br /><br /><br /></h3>
                    </section><section>
                    <h2>Költözés szegedre</h2>
                    <h3>Hazautazásom után folytattam a munkát nagyapámmal, igaz nem túl sokáig, ugyanis 2018 októberében úgy alakult a dolog, hogy nővéremnek, aki akkor már egy éve Szegeden élt, lakótársa lenne szüksége. <br />
                    Akkor az volt a terv, hogy a szezon végéig dolgozom nagyapámmal, majd az új évre szegedre költözöm, ott keresek munkát. Így közelebb maradok a családomhoz és a nővérem is Szegeden él, így lenne társaságom. <br />
                    Ekkor égi jelnek tekintettem, hogy pont mellé tudnék költözni, sokkal kényelmesebb, mint egy random idegen mellett élni. <br />
                    Igaz, ekkor még nem volt munkám, de egy hónappal korábban láttam egy hirdetést informatikai állásra, így hát gondoltam megpróbálom, hátha még keresnek embert.<br /><br /><br /></h3>
                    <h2>Csatlakozás a DARTKE-hez</h2>
                    <h3>Szerencsével jártam, még tartott a toborzás a Délalföldi Regionális Társadalomtudományi Kutatási Egyesülethez. Nem volt egyéb kritérium, csak az érettségi és a 25 év alatti korhatár. <br />
                    Felvettek és pár héten belül be is osztottak, bár webprogramozónak mentem volna, de grafikus lettem. <br />
                    Középiskola óta hobbi szerűen foglalkoztam webprogramozással, HTML, CSS, PHP, MySQL környezetekben, ezért volt bátorságom megpróbálkozni ebben a kategóriában. Viszont kiderült, hogy van érzékem a rajzoláshoz, így abban a pozícióban helyeztek el. <br />
                    Még általános iskolában kiderült, hogy van egy kis tehetségem a képzőművészethez. Ekkor volt egy olyan tervem, hogy képzőművészeti gimnáziumba megyek, Zentára, de gyorsan elvetettük közös megegyezés alapján szüleimmel. Úgy gondoltuk bölcsebb lenne egy szakmát szerezni, hogyha valami miatt nem tudnék egyetemre menni középiskola után, legalább szakmai papírom legyen. <br />
                    Viszont most visszatért ez a tehetség, gyorsan kiderült az új munkahelyemen és használatba is vették.<br /><br /><br /></h3>
                    </section><section>
                    <h2>Garfikai pálya</h2>
                    <h3>Azóta már harmadik éve dolgozom grafikusként. Leginkább webdizájn, logó tervezés, arculatok tervezése a főbb feladatom, illetve sokat foglalkozunk 2D-s animációk, promóciós videók készítésével is. Ez utóbbiak közelebb állnak hozzám. <br />
                    A középiskolában leginkább CAD modellezéssel foglalkoztunk, ezt és a munkám során szerzett tapasztalataimat szeretném a jövőben átdolgozni, összegyúrni 3D-s animációk készítésére. <br />
                    Ezen dolgozom jelenleg a rajzolás, arculatok, plakátok tervezése mellett.<br /><br /><br /></h3>
                    <h2>Egyéb szbadidős tevékenységek</h2>
                    <h3>A felsoroltak után fennmaradó szabadidőmben többnyire kerékpározom, rosszabb időkben pihenési jelleggel egy régóta tartó függőségemnek teszek eleget: World of Warcraft-ozom, vagy próbálok a munka mellett még egy kis energiát ölni önfejlesztésbe, modellezésbe.<br /><br /><br /><br /></h3>
                    </section>
                </div>
            </div>
        </div>
    )
}

export default AboutBody;